body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background: url(/bg.jpg) no-repeat center center fixed;
  /* Background Link https://unsplash.com/photos/JgOeRuGD_Y4 */
  background-size: cover;
}


/* Large desktops and laptops */
@media (min-width: 1200px) {
  .contact-small {
    display: none;
  }
  .contact {
    display: block;
  }
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .contact-small {
    display: none;
  }
  .contact {
    display: block;
  }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .contact-small {
    display: block;
  }
  .contact {
    display: none;
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .contact-small {
    display: block;
  }
  .contact {
    display: none;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .contact-small {
    display: block;
  }
  .contact {
    display: none;
  }
}

.app {
  margin: 50px;
  font-family: 'Roboto';
  text-align: center;
  color: white;
}

.card {
  width: 400px;
  margin: 20px;
  background-color: white;
  font-size: 20px;
  border-radius: 15px;
  color: black;
  text-align: left;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, .2);
  -webkit-transition: 150ms ease all;
  transition: 150ms ease all;
}
.card:hover {
  cursor: pointer;
  box-shadow: 5px 15px 20px rgba(0, 0, 0, .6);
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
.card-pic {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, .1);
}
.card-title {

}
.card-desc {
  margin-top: 12px;
  text-align: left;
  font-size: 14px;
}
.card-body {
  padding: 20px;
}

.pic {
  border-radius: 50%;
  width: 200px;
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  box-shadow: 0px 15px 20px rgba(0, 0, 0, .2);
}

.name {
  color: white;
  font-size: 32px;
  margin-top: 30px;
}
.bio {
  margin-top: 20px;
}
.bio > a {
  cursor: pointer;
  color: white;
}
.bio > a:hover {
  text-decoration: underline;
}

.italic {
  font-style: italic;
}

.row-cards {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.row {
  display: flex;
  align-items: center;
}

.row > a {
  margin-bottom: 3px;
}

.cards-wrap {
  margin-top: 50px;
}

.github-logo {
  width: 16px;
  margin-left: 5px;
  -webkit-transition: 250ms linear all;
  transition: 250ms linear all;
}
.github-logo:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
  opacity: .8;
}

.contact {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: rgb(50, 50, 255);
  border-radius: 40px;
  position: fixed;
  bottom: 15px;
  right: 15px;
  -webkit-transition: 250ms ease all;
  transition: 250ms ease all;
  z-index: 999;
}
.contact-small {
  width: 30px;
  height: 30px;
  padding: 10px;
  background-color: rgb(50, 50, 255);
  border-radius: 50%;
  position: fixed;
  bottom: 15px;
  right: 15px;
  -webkit-transition: 250ms ease all;
  transition: 250ms ease all;
  z-index: 999;
}
.contact-small:hover {
  cursor: pointer;
  box-shadow: 0px 2px 15px rgba(50, 50, 255, .6);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.contact:hover {
  cursor: pointer;
  box-shadow: 0px 2px 15px rgba(50, 50, 255, .6);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

footer {
  margin-top: 100px;
  margin-bottom: 100px;
  text-align: center;
}
footer > a {
  text-decoration: underline;
  color: white;
}

.social-icon {
  color: white;
  margin-left: 5px;
  margin-right: 5px;
}

.contact-form {
  margin: 50px;
  font-family: Arial;
  font-size: 18px;
}
.contact-title {
  font-weight: bold;
  font-size: 26px;
  margin-bottom: 20px;
}
.contact-send {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: rgb(50, 50, 255);
  border-radius: 40px;
  color: white;
  border: 0px;
  margin-top: 20px;
  -webkit-transition: 250ms ease all;
  transition: 250ms ease all;
}
.contact-send:hover {
  cursor: pointer;
  box-shadow: 0px 5px 10px rgba(50, 50, 255, .3);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.modal-input {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  border: 0px;
  border-bottom: 1px solid rgba(0, 0, 0, .3);
  padding: 10px;
}
.modal-input:focus {
  border-bottom: 1px solid blue;
}
.modal-textarea {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  border: 0px;
  border-bottom: 1px solid rgba(0, 0, 0, .3);
  padding: 10px;
  font-family: Arial;
}
.modal-textarea:focus {
  border-bottom: 1px solid blue;
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
  text-decoration: none;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

html,
button,
input,
select,
textarea {
    color: #222;
}


::-moz-selection {
    background: #b3d4fc;
    text-shadow: none;
}

::selection {
    background: #b3d4fc;
